    <asideNavBar />
    <section class="right-side" >
            <headerNav />
            <section class="main-content" style="justify-content: start;align-items: start;">
                <header class="crud-title" id="top">
                    <h2>Chat conferencia</h2>
                </header>
               

                <section class="produto-section">
                    <ul>
                        <li>
                            
                            <!-- <produtoCard :propsData="{data: item}" /> -->
                        </li>
                        
                    </ul>
                </section>
            </section>
