</template>

<script>
    import ProdutoCard from '@/components/produtoCard.vue'
    import axios from 'axios';
    import { BASE_URL, KEY } from '@/config';

    export default {

        data() {
            return {
                idField: 0,
                fotoField: '',
                nomeField: '',
                descricaoField: '',
                categoria_id: 0,
                tipo_id: 0,
                produtos: [],
                categorias: [],
                tipos: [],
                selectedFoto: null,

                action: 'Registrar'
            }
        },
        components:{
            ProdutoCard,
            
        },
        watch:{

            fotoField(){
                console.log('Foto: ',this.fotoField);
            },

            '$store.state.produto.data': {
                handler(newVal, oldVal) {
                    console.log('Produto: ', newVal);
                    this.idField = newVal.id ?? 0;
                    this.nomeField = newVal.nome ?? '';
                    this.descricaoField = newVal.descricao ?? '';

                    this.scrollToSection('top')
                    document.getElementById('img').src = newVal.foto ?? '../assets/sem-foto.png';




                },
                deep: true
            },

            '$store.state.produto.updated': {
                handler(newVal, oldVal) {
                    this.getAllProdutos()
                },
                deep: true
            },
        },

        computed: {

            formData(){
                return {
                    id: this.idField,
                    nome: this.nomeField,
                    descricao: this.descricaoField,
                    foto: this.fotoField,
                    // categoria_id: this.categoria_id,
                    // tipo_id: this.tipo_id
                }
            },

            imgSrc(url){
                // return  `${BASE_URL}/produto/foto/${this.$store.state.produto?.data.id}/?key=121212121`;
                return  `${BASE_URL}/${url}/?key=121212121`;
            },
        
        },
        
        methods:{
            handleFileUpload(event) {
                const file = event.target.files[0];
                console.log(file);
            },

            scrollToSection(id) {
                document.getElementById(id).scrollIntoView({ behavior: 'smooth' });
            },

            onSelectFoto(event){
                this.selectedFoto = event.target.files[0];
                // document.querySelector('img').src = URL.createObjectURL(this.selectedFoto);
                document.getElementById('img').src = URL.createObjectURL(this.selectedFoto);
            },

            async createProduto(){

                try{
                    let formulario = new FormData()
                    formulario.append('nome', this.nomeField)
                    formulario.append('descricao', this.descricaoField)
                    // formulario.append('foto', this.fotoField)
                    formulario.append('foto', this.selectedFoto)

                    // const res = await axios.post(`${BASE_URL}/produto/registrar/?key=${KEY}`,this.formData)
                    const res = await axios.post(`${BASE_URL}/produto/registrar/?key=${KEY}`, formulario,{
                        // nome: this.nomeField,
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    })
                    console.log(res.data);
                    this.$swal({
                        title: 'Sucesso!',
                        text: 'Produto registrado com sucesso',
                        icon: 'success',
                        confirmButtonText: 'OK',
                        timer: 3000,
                        showCancelButton: false,
                        cancelButtonText: 'Cancelar',
                        reverseButtons: false
                    })
                    this.getAllProdutos()
                
                }catch(error){

                    console.log(error?.response ?? error);
                    this.$swal({
                        title: 'Error!',
                        text: 'Falha ao registrar Produto',
                        icon: 'error',
                        confirmButtonText: 'OK',
                        timer: 3000,
                        showCancelButton: false,
                        cancelButtonText: 'Cancelar',
                        reverseButtons: false
                    })
                }

            },
            
            async updateProduto(id){
                let formulario = new FormData()
                formulario.append('nome', this.nomeField)
                formulario.append('descricao', this.descricaoField)
                formulario.append('foto', this.selectedFoto)
               


                try {
                    // const res = await axios.put(`${BASE_URL}/produto/${this.$store.state.produto.data.id}/atualizar/?key=${KEY}`,this.formData)
                    const res = await axios.put(`${BASE_URL}/produto/editar/${this.idField}/?key=${KEY}`,formulario, {

                        headers: {
                            'Content-Type': 'multipart/form-data'
                        },
                        // id: this.idField,
                        // nome: this.nomeField,
                        // descricao: this.descricaoField,
                        // foto: this.fotoField,
                        // categoria_id: this.categoria_id,
                        // tipo_id: this.tipo_id
                    })
                    console.log(res.data);
                    this.$swal({
                        title: 'Sucesso!',
                        text: 'Produto atualizado com sucesso',
                        icon: 'success',
                        confirmButtonText: 'OK',
                        timer: 3000,
                        showCancelButton: false,
                        cancelButtonText: 'Cancelar',
                        reverseButtons: false
                    })
                    this.getAllProdutos()
                } catch (error) {
                    console.log(error?.response ?? error);
                    this.$swal({
                        title: 'Error!',
                        text: 'Falha ao atualizar Produto',
                        icon: 'error',
                        confirmButtonText: 'OK',
                        timer: 3000,
                        showCancelButton: false,
                        cancelButtonText: 'Cancelar',
                        reverseButtons: false
                    })
                }
            },

            async submitForm(id = null){

                if (this.$store.state.produto.action == 'create') {
                    this.createProduto()
                }else{
                    this.updateProduto(id)
                }

            },

            async getAllProdutos(){
                try {
                    const res = await axios.get(`${BASE_URL}/produto/?key=${KEY}`)
                    this.produtos = res.data.data
                    console.log('Dados de fun carregados', this.produtos);
                    
                } catch (error) {
                    console.log(error?.response ?? error);
                    this.$swal({
                        title: 'Error!',
                        text: 'Falha ao listar Produtos',
                        icon: 'error',
                        confirmButtonText: 'OK',
                        timer: 3000,
                        showCancelButton: false,
                        cancelButtonText: 'Cancelar',
                        reverseButtons: false
                    })
                }
                
               
            },
            
    
        },

        mounted(){
            // console.log('Mounted');
           

        },

        created() {
            // this.getTipos()
           
        },

    
        
        
    }

</script>

